import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'

//debug
Vue.config.debug = true;

Vue.use(VueRouter);
Vue.use(VueResource);
// const first = { template: '<div><h2> 1 </h2><router-link to="/first/child">123</router-link><router-view></router-view></div>' }
import first from './component/first/first.vue'
import next from './component/next/next.vue'
import ajaxPage from './component/second/ajax.vue'
const aa = { template: '<h2>{{$route.params.id}}</h2>' }

const router = new VueRouter({
    mode: 'history',
    base: __dirname,
    routes: [{
        path: '/first',
        component: first,
        children: [{
            path: '/first/list/:id',
            name: 'list',
            component: aa
        }, {
            path: '/first/list1/:id',
            name: 'list1',
            component: aa
        }]
    }, {
        path: '/next',
        component: next
    }, {
        path: '/ajaxPage',
        component: ajaxPage
    }]
})

new Vue({
    el: '#app',
    router: router,
    render: h => h(App)
})



